<template>
    <div id="Refresh">
        <img src="../../assets/images/shuaxin.svg" width="20pt" height="20pt" id="Picture">
        <p id="text">正在刷新...</p>
    </div>
</template>
<script>
export default {
    name:'Refresh'
}
</script>
<style scoped>
#Refresh
{
    display:none;
    justify-content: center;
    align-items: center;
    animation-name:refresh;
    animation-duration:0.5s;
}
p
{
    font-size:14pt;
}
#Picture
{
    animation-name:rorate;
    animation-duration:0.5s;
    animation-iteration-count:infinite;
    animation-timing-function: linear;
}
@keyframes refresh{
    from{
        transform:scale(0.1);
    }
    to{
        transform:scale(1);
    }
}
@keyframes rorate{
    from{
        transform:rotate(0deg);
    }
    to{
        transform:rotate(360deg);
    }
}
</style>