<template>
  <div class="Post">
    <li v-for="item in information" :key="item.name" @click="reserve(item.id)">
      <router-link to="Posts/Postdetails" style="text-decoration: none">
        <div id="smallTitle">
          <div id="square">
            <img
              src="../../assets/images/GeRenZhongXin.svg"
              width="40pt"
              height="40pt"
            />
          </div>
          <div id="smallTitleContent">
            <p class="titleText">{{ item.posterName }}</p>
            <p class="titleText">{{ item.postTime }}</p>
          </div>
        </div>
        <div class="content">
          <div class="label">
            <p class="labelText">出发地</p>
          </div>
          <p>{{ item.startLocation }}</p>
        </div>
        <div class="content">
          <div class="label">
            <p class="labelText">目的地</p>
          </div>
          <p>{{ item.destination }}</p>
        </div>
        <div class="content">
          <div class="label">
            <p class="labelText">拼车时间</p>
          </div>
          <p>
            {{
              item.year +
              "年" +
              item.month +
              "月" +
              item.day +
              "日" +
              item.hour +
              "时" +
              item.minute +
              "分"
            }}
          </p>
        </div>
        <div class="content" style="padding-bottom: 5pt">
          <div class="label">
            <p class="labelText">描述</p>
          </div>
          <p>{{ item.content }}</p>
        </div>
      </router-link>
    </li>
  </div>
</template>
<script>
export default {
  name: "Post",
  data() {
    return {
      information: [],
    };
  },
  methods: {
    reserve: function (itemId) {
      localStorage.setItem("postNumber", itemId);
    },
  },
  mounted() {
    if (sessionStorage.length == 0) {
      fetch("http://180.76.100.8/posts/flag/0", {
        method: "GET",
      })
        .then((response) => response.json())
        .then((res) => {
          this.information = res.data;
        });
        console.log(JSON.parse(sessionStorage.getItem("test")));
    }
     else if (sessionStorage.getItem("test") == '[]'&&sessionStorage.length !== 0) {
         this.information = JSON.parse(sessionStorage.getItem("test"));
      alert("未查询到任何数据!");
      console.log(JSON.parse(sessionStorage.getItem("test")));
    }
     else {
      this.information = JSON.parse(sessionStorage.getItem("test"));
      console.log(JSON.parse(sessionStorage.getItem("test")));
    }
  },
};
</script>
<style scoped>
li {
  list-style: none;
  margin-top: 10pt;
  background-color: white;
  border-radius: 5pt;
}
.Post {
  margin: 5pt;
  margin-top: 10pt;
  border-radius: 5pt;
}
.content {
  display: flex;
  flex-direction: row;
  padding-left: 10pt;
  align-items: center;
  margin-top: 5pt;
}
#square {
  width: 40pt;
  height: 40pt;
  border-radius: 50%;
  background-color: pink;
  display: flex;
  justify-content: center;
  align-items: center;
}
#smallTitle {
  display: flex;
  padding-top: 10pt;
  padding-left: 10pt;
}
#smallTitleContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.label {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #a49bb5;
  margin-right: 5pt;
  border-radius: 5pt;
  padding-right: 5pt;
  padding-left: 5pt;
}
p {
  color: black;
}
.labelText {
  color: white;
}
.titleText {
  padding-left: 5pt;
}
</style>