<template>
  <div id="Posts" @touchstart="refresh()" @touchend="prevent()">
    <Title
      title="拼车"
      cutDisplay="none"
      finishDisplay="none"
      complainDisplay="none"
      finishDisplay1="none"
      finishDisplay2="none"
      finishDisplay3="none"
    />
    <Search />
    <Refresh />
    <div id="information">
      <Post />
    </div>
    <div id="empty"></div>
  </div>
</template>
<script>
import Post from "../components/Posts/Post.vue";
import Title from "../components/Public/Title.vue";
import Search from "../components/Public/Search.vue";
import Refresh from "../components/Public/Refresh.vue";
export default {
  name: "Posts",
  components: {
    Post,
    Title,
    Search,
    Refresh,
  },
  data() {
    return {
      nothing: "",
      another: "",
    };
  },
  methods: {
    refresh: function () {
      if (window.scrollY == 0) {
        this.another = setTimeout(
          "document.getElementById('Refresh').style.display='flex';",
          500
        );
        this.nothing = setTimeout("location.reload();", 1000);
      }
    },
    prevent: function () {
      if (window.screenY == 0) {
        clearTimeout(this.another);
        clearTimeout(this.nothing);
        document.getElementById("Refresh").style.display = "none";
      }
    },
  },
};
</script>
<style scoped>
#Posts {
  width: 100%;
  background-color: #f7f7f8;
  min-height: 100%;
}
#empty {
  height: 50pt;
}
</style>