<template>
  <div id="Search">
    <select id="select" v-model="searchKey" @change="test()">
      <option value="" id="top" class="option">
        搜索选项
      </option>
      <option value="startlocation" class="option">出发地</option>
      <option value="destination" class="option">目的地</option>
      <option value="content" class="option">描述</option>
      <option value="nickname" id="bottom" class="option">发起人</option>
    </select>
    <input v-model="searchValue" placeholder="搜搜看..." />
    <div id="Content" @click="searc()">
      <img src="../../assets/images/sousuo.svg" width="25pt" height="25pt" />
    </div>
  </div>
</template>
<script>
export default {
  name: "Search",
  data() {
    return {
      searchKey: "",
      searchValue: "",
    };
  },
  methods: {
    searc: function () {
      if (this.searchValue !== ""&&this.searchKey!=="") {
        fetch(
          "http://180.76.100.8/posts/search/" +
            this.searchKey +
            "/" +
            this.searchValue,
          {
            method: "GET",
          }
        )
          .then((res) => res.json())
          .then((res) => {
            sessionStorage.setItem("test", JSON.stringify(res.data));
          });
        setTimeout('location.reload()',1);
      }
       else {
        sessionStorage.removeItem("test");
        setTimeout('location.reload()',1);
      }
    },
    test:function(){
      console.log(this.searchKey);
      console.log(this.searchValue);
    }
  },
};
</script>
<style scoped>
input {
  width: 75%;
  border: none;
  outline: none;
  font-size: 12pt;
  border-top-left-radius: 15pt;
  border-bottom-left-radius: 15pt;
}
#Search {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin: 5pt;
  border-radius: 15pt;
  background: white;
  position: relative;
  top: -2.5%;
}
#Content {
  background-color: #b3acc1;
  width: 45pt;
  height: 30pt;
  border-radius: 15pt;
  display: flex;
  justify-content: center;
  align-items: center;
}
#select {
  border-radius: 5pt;
  outline: none;
  border: none;
  background:none;
}
#top {
  border-top-left-radius: 5pt;
  border-top-right-radius: 5pt;
}
.option {
  outline: none;
}
</style>